h1,
h2,
h3,
h4,
h5,
h6,
p {
  padding: 0;
  margin: 0;
}

ul,
li {
  list-style: none;
  padding: 0;
  margin: 0;
}

a {
  display: block;
  text-decoration: none;
  color: currentColor;
}

img {
  display: block;
  margin-bottom: 0;
  padding-bottom: 0;
  max-width: 100%;
  height: auto;
}

input,
textarea {
  outline: transparent;
}
